<template>
  <div id="app">
    <img src="./assets/Pizza_logo.jpg">
    
    <div id="page1">
      <h1>Podaj swój adres: </h1>
      <input type="text" placeholder="Podaj adres" v-model="newAddress">
      <button @click="addAddress()">Dodaj adres</button>
      <h1>Zapisany adres: {{ Address }}</h1>  
    </div>

    <div id="page2">
      <h1>Wybierz swoją Pizzę: </h1>
      <div class="pizza" 
      v-bind:class="{
        wybrana: pizza.wybrana
        }"
        v-for="pizza in pizze" 
        v-bind:key="pizza.id">
        <h2>{{ pizza.id }}. {{ pizza.title }}</h2>
        <h3>{{ pizza.skladniki }}</h3>
        <button1 @click="dodajPizze(pizza.id)">Dodaj do koszyka</button1>
        <ul></ul>
        <button2 v-if="pizza.wybrana" @click="usunPizze(pizza.id)">Usuń z koszyka</button2>
        <h4>{{ pizza.cena }}zł</h4>
      </div>
      <h1>Wartość zamówienia: {{wartosc}}zł</h1>  
    </div>

    <div id="page3">
      <form class="contact-form" @submit.prevent="sendEmail">
      <h1>Podsumowanie zamówienia: </h1>
      <h2>Podaj swoje dane osobowe: </h2>
      <input type="hidden" name="choosen_pizza" v-model="wybrane_pizze">
      <input type="hidden" name="order_number" v-model="order_number">
      <ul><input type="text" name="user_name" placeholder="Imię"></ul>
      <ul><input type="text" name="user_surname" placeholder="Nazwisko"></ul>
      <input type="hidden" name="user_address" v-model="Address">
      <ul><input type="text" name="user_phone" placeholder="Numer telefonu"></ul>
      <ul><input type="text" name="user_email" placeholder="Adres e-mail"></ul>
      <ul><textarea name="message" placeholder="Dodatkowe uwagi:"></textarea></ul>
      <input type="hidden" name="total_cost" v-model="wartosc">
      <button type="submit" @click="potwierdzenie">Wyślij zamówienie</button>
      </form>
    </div>
    <button @click="nowe_zamowienie">Nowe zamówienie</button>

    <footer class="footer"><ul></ul>Designed by Leonard Lech
    <ul>Projektowanie Aplikacji Internetowych 2 - 2020</ul></footer>
  </div>
</template>

<script>

import emailjs from 'emailjs-com';

export default {
  data() {
    return {
      newAddress: undefined,
      Address: undefined,
      wartosc: 0,
      wybrane_pizze: ' ',
      pizze: [
        { title: 'Margherita', skladniki: 'Sos pomidorowy, ser', cena: 20, wybrana: false, id: 1},
        { title: 'Polo', skladniki: 'Sos pomidorowy, ser, kurczak', cena: 22, wybrana: false, id: 2},
        { title: 'Hawajska', skladniki: 'Sos pomidorowy, ser, ananas, szynka', cena: 23, wybrana: false, id: 3},
        { title: 'Grecka', skladniki: 'Sos pomidorowy, ser, oliwki, ser feta, pomidor', cena: 25, wybrana: false, id: 4},
        { title: 'Rucola', skladniki: 'Sos pomidorowy, ser, szynka parmeńska, pomidor, rucola, parmezan', cena: 26, wybrana: false, id: 5},
        { title: 'Pepperoni', skladniki: 'Sos pomidorowy, ser, cebula, salami, pieczarki, chilli, papryka', cena: 27, wybrana: false, id: 6},
        { title: 'Napoli', skladniki: 'Sos pomidorowy, ser, kurczak, szynka, kukurydza, papryka, pieczarki', cena: 28, wybrana: false, id: 7},
        { title: 'Diablo', skladniki: 'Sos pomidorowy, ser, tabasco, salami, boczek, chilli, papryka, pieczarki, szynka', cena: 30, wybrana: false, id: 8}
        ],
        order_number: Math.floor(Math.random()*10000000),
    }

  },
  methods: {
    addAddress() {
      this.Address= this.newAddress
    },
    dodajPizze(id){
      const index = this.pizze.findIndex(el => el.id === id)
      this.pizze[index].wybrana = true
      this.wartosc=this.wartosc+this.pizze[index].cena
      if (this.pizze[index].wybrana === true) {
        this.wybrane_pizze = this.wybrane_pizze + ' ' + this.pizze[index].title
        }
    },
    usunPizze(id){
      const index = this.pizze.findIndex(el => el.id === id)
      this.wartosc=this.wartosc-this.pizze[index].cena
      this.wybrane_pizze = this.wybrane_pizze - this.pizze[index].title
      if (this.wartosc <= 0) {
        this.pizze[index].wybrana = false
        this.wybrane_pizze = ' '
      }
      if (this.wartosc < 0){
        this.wartosc = 0
        this.wybrane_pizze = ' '
      }
    },
    sendEmail: (e) => {
      emailjs.sendForm('contact_service', 'contact_form', e.target, 'user_4t4OdENSE8dLo4RBFsrpi')
        .then((result) => {
          console.log('SUCCESS!', result.status, result.text);
          }, (error) => {
          console.log('FAILED...', error);
          });
    },
    potwierdzenie() {
      alert('Zamówienie zostało potwierdzone i wysłane!')
    },
    nowe_zamowienie() {
      window.location.reload()
      window.scrollTo(0,0)
    }
  }
}
</script>

